import { Tarifa } from '../modelo/tarifa';
import { Observable} from 'rxjs';
import { Plaza } from '../modelo/plaza';
import { Ticket } from './../modelo/ticket';
import { TicketWrapper } from '../modelo/ticket-wrapper';

export abstract class TicketService {

  abstract getTarifa(url): Observable<Tarifa>;

  abstract getPlaza(url): Observable<Plaza>;

  abstract getTickets(): Observable<Ticket[]>;

  abstract getTicketNoPagados(): Observable<Ticket[]>;

  abstract crearTicket(matricula: string): Observable<TicketWrapper> ;

  abstract pagarTicket(id: number): Observable<TicketWrapper>;

}
